import React, { useEffect, useState } from 'react';

// material-ui
import {
    Avatar,
    CardContent,
    CardActions,
    Card,
    Stack,
    Typography,
    Container,
    Chip,
    Grid,
    Button,
    TextField,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Paper,
    ListItemAvatar,
    ListItemButton,
    ListItemSecondaryAction,
    Tooltip,
    List,
    ListItem,
    IconButton,
    ListItemText,
    Divider
} from '@mui/material';

import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';

import AccountTreeIcon from '@mui/icons-material/AccountTree';
import AssignmentIcon from '@mui/icons-material/Assignment';
import PeopleIcon from '@mui/icons-material/People';
import FeedIcon from '@mui/icons-material/Feed';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import DoneIcon from '@mui/icons-material/Done';
import { makeStyles } from '@mui/styles';
import { useContext } from 'react';
import { AuthContext } from 'src/App';
import axios from 'axios';
import { Checkbox, FormControl, MenuItem, OutlinedInput, Select, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { apiBaseURL } from 'src/config-global';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { collection, firestore } from 'src/Firebase';
import { useTheme } from '@mui/styles';
import { useAuthContext } from 'src/auth/useAuthContext';
import firebase from 'firebase';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
function getStyles(name, personName, theme) {
    return {
        fontWeight: personName.indexOf(name) === -1 ? theme.typography.fontWeightRegular : theme.typography.fontWeightMedium
    };
}
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250
        }
    }
};

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div role="tabpanel" hidden={value !== index} id={`simple-tabpanel-${index}`} aria-labelledby={`simple-tab-${index}`} {...other}>
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`
    };
}

const allProjects = ['itadmin', 'admin']

const SamplePage = () => {
    const { user } = useAuthContext();
    const theme = useTheme();

    const [tabValue, setTabValue] = React.useState(0);

    const [value, setValue] = React.useState(0);
    const [open, setOpen] = React.useState(false);

    const handleChange = (event, newValue) => {
        setTabValue(newValue);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const [tasks, setTasks] = useState([]);
    const fetchTasks = () => {
        // axios.get(`${apiBaseURL}/manager/emptask/${user.company}?key=employee&value=${user.email}`).then((res) => {
        //     // console.log(res.data);
        //     setTasks(res.data);
        // });
        axios.get(`${apiBaseURL}/project/tasks?email=${user.email}`).then((res) => {
            const goalList = res.data
            const temp = []
            goalList.forEach(({_id: goalId, tasks}) => {
                tasks.forEach(task => {
                    if(task.members.includes(user.email)) temp.push({...task, goalId})
                })
            })
            // console.log(temp)
            setTasks(temp);
        });
    };

    const [projects, setProjects] = useState([]);
    const fetchProjects = async () => {
        if(allProjects.includes(user.role)){
            // fetch all projects in company if admin/itadmin
            axios.get(`${apiBaseURL}/project/${user.company}`).then((res) => {
                setProjects(res.data);
            });   
        } else{
            // fetch manager's projects
            if(user.role === 'manager'){
                axios.get(`${apiBaseURL}/project/${user.company}?managerId=${user.email}`).then((res) => {
                    setProjects(res.data);
                });    
            }
            else{
                // fetch employee's projects
                const managers = user.firestore.projects
                if (!managers) return
                setProjects([])
                managers.map(managerId => {
                    axios.get(`${apiBaseURL}/project/${user.company}?managerId=${managerId}`).then((res) => {
                        setProjects(prev => ([
                            ...prev, ...res.data
                        ]));
                    });  
                })
            }
        }
    };

    const handleTaskStatus = (goalId, _id) => (e) => {
        const status = e.target.checked;
        const payload = {taskId: _id, status}
        axios.patch(`${apiBaseURL}/project/goal/${goalId}`, {payload}).then((res) => {
            if (res.data === 'Updated') {
                fetchTasks();
            }
        });
    };

    const [newProject, setNewProject] = useState({
        name: '',
        desc: ''
    });
    const handleProjectInput = (field) => (e) => {
        setNewProject((prev) => ({
            ...prev,
            [field]: e.target.value
        }));
    };

    const [projectTeam, setProjectTeam] = useState([])
    const getProjectTeam = async () => {
        const snapshot = await firestore.collection(collection).where('projects', 'array-contains', user.email).get()
        const temp = []
        snapshot.forEach((doc) => {
            let data = doc.data();
            if (doc.id !== user.uid) {
                data.uid = doc.id;
                temp.push(data);
            }
        });
        setProjectTeam(temp)
    }

    const handleDialogSubmit = (tab) => async () => {
        if (tab === 0) {
            const formData = {
                managerId: user.email,
                company: user.company,
                ...newProject
            };
            const res = await axios.post(`${apiBaseURL}/project`, { formData });
            if (res.data === 'Created') {
                fetchProjects();
                setNewProject({
                    name: '',
                    desc: ''
                });
                toast.success('New project added');
            } else {
                toast.error(res.data);
            }
        } else if (tab === 2) {
            const temp = allEmployees.filter((item) => invites.includes(item.email));
            const promises = [];
            temp.forEach((item) => {
                promises.push(
                    firestore
                        .collection(collection)
                        .doc(item.uid)
                        .update({ projects: firebase.firestore.FieldValue.arrayUnion(user.email) })
                );
            });
            Promise.all(promises).then((res) => {
                getProjectTeam()
                setInvites([])
                console.log('Team Updated');
            });
        }
        handleClose();
    };

    const tabFunctions = new Map([
        [0, fetchProjects],
        [1, fetchTasks],
        [2, getProjectTeam],
        [3, null]
    ]);

    useEffect(() => {
        if (!user) return;
        const cb = tabFunctions.get(tabValue);
        if (cb) cb();
    }, [tabValue]);

    const [invites, setInvites] = useState([]);
    const newInvites = (event) => {
        const {
            target: { value }
        } = event;
        setInvites(value);
    };

    const [allEmployees, setAllEmployees] = useState([]);
    const getAllEmployees = () => {
        const queryRef = firestore.collection(collection).where('company', '==', user.company).where('role', '==', 'employee');
        const temp = [];
        queryRef.get().then((snapshot) => {
            snapshot.forEach((doc) => {
                let data = doc.data();
                if (doc.id !== user.uid) {
                    data.uid = doc.id;
                    temp.push(data);
                }
            });
            setAllEmployees(temp);
        });
    };

    return (
        <>
            <Dialog
                open={open}
                onClose={handleClose}
                maxWidth="sm"
                fullWidth
                scroll="body"
                PaperProps={{ sx: { mt: '100px', verticalAlign: 'top' } }}
            >
                {value === 0 ? (
                    <>
                        <DialogTitle variant="h3">Add Project</DialogTitle>
                        <DialogContent>
                            <DialogContentText variant="h5">Project Name</DialogContentText>
                            <TextField
                                autoFocus
                                margin="dense"
                                id="project-name"
                                placeholder="Mission 101"
                                fullWidth
                                variant="outlined"
                                value={newProject.name}
                                onChange={handleProjectInput('name')}
                            />
                            <br />
                            <br />
                            <DialogContentText variant="h5">Brief Description</DialogContentText>
                            <TextField
                                margin="dense"
                                id="project-desc"
                                multiline
                                rows={4}
                                placeholder="Started with ..."
                                fullWidth
                                variant="outlined"
                                value={newProject.desc}
                                onChange={handleProjectInput('desc')}
                            />
                        </DialogContent>
                    </>
                ) : null}
                {value === 2 ? (
                    <>
                        <DialogTitle variant="h3">Invite new user/s </DialogTitle>
                        <DialogContent>
                            <DialogContentText variant="h5">Select Employees</DialogContentText>
                            <FormControl fullWidth>
                                <Select
                                    id="demo-multiple-chip"
                                    multiple
                                    value={invites}
                                    onChange={newInvites}
                                    input={<OutlinedInput id="select-multiple-chip" label="Managers" />}
                                    renderValue={(selected) => (
                                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                            {selected.map((value) => (
                                                <Chip key={value} label={value} />
                                            ))}
                                        </Box>
                                    )}
                                    MenuProps={MenuProps}
                                >
                                    {allEmployees.map((item) => {
                                        if (item.projects?.includes(user.email)) return;
                                        return (
                                            <MenuItem
                                                key={item.email}
                                                value={item.email}
                                                style={getStyles(item.email, allEmployees, theme)}
                                            >
                                                {`${item.displayName} (${item.email})`}
                                            </MenuItem>
                                        );
                                    })}
                                </Select>
                            </FormControl>
                        </DialogContent>
                    </>
                ) : null}
                <DialogActions>
                    <Button onClick={handleClose}>Cancel</Button>
                    <Button onClick={handleDialogSubmit(value)}>Add</Button>
                </DialogActions>
            </Dialog>

            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                {/* <Grid item xs={12}>
                    <Typography sx={{ float: 'left' }} variant="h3">
                        Projects
                    </Typography>
                </Grid> */}

                <Grid item xs={12}>
                    <Box sx={{ width: '100%' }}>
                        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                            <Tabs value={tabValue} onChange={handleChange} aria-label="basic tabs example">
                                <Tab icon={<AccountTreeIcon />} iconPosition="start" label="Projects" {...a11yProps(0)} />
                                <Tab icon={<AssignmentIcon />} label="My Tasks" iconPosition="start" {...a11yProps(1)} />
                                <Tab icon={<PeopleIcon />} label="Team" iconPosition="start" {...a11yProps(2)} />
                                {/* <Tab icon={<FeedIcon />} label="Feed" iconPosition="start" {...a11yProps(3)} /> */}
                            </Tabs>
                        </Box>
                        <TabPanel value={tabValue} index={0}>
                            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                                <Grid item xs={12}>
                                    <Typography sx={{ float: 'left' }} variant="h3">
                                        Projects
                                    </Typography>
                                    <Button
                                        variant="contained"
                                        startIcon={<AddIcon />}
                                        sx={{ float: 'right' }}
                                        onClick={() => {
                                            setValue(0)
                                            setOpen((open) => !open)
                                        }}
                                    >
                                        Add a new project
                                    </Button>
                                </Grid>

                                <Grid item xs={12}>
                                    {projects.map((item) => {
                                        return (
                                            <>
                                                <Card key={item._id} sx={{ mb: 2 }} variant="outlined">
                                                    <CardContent>
                                                        <Typography variant="h5">
                                                            <Link
                                                                style={{ textDecoration: 'none' }}
                                                                to={`/dashboard/projectview?id=${item._id}`}
                                                                state={{ title: item.name }}
                                                            >
                                                                {item.name}
                                                            </Link>
                                                        </Typography>
                                                        <Typography variant="body1">{item.desc}</Typography>
                                                    </CardContent>
                                                </Card>
                                            </>
                                        );
                                    })}
                                </Grid>
                            </Grid>
                        </TabPanel>
                        <TabPanel value={tabValue} index={1}>
                            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                                <Grid item xs={12}>
                                    <Typography sx={{ float: 'left' }} variant="h3">
                                        Tasks assigned to you
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <TableContainer component={Paper}>
                                        <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                            <TableHead>
                                                <TableRow>
                                                    <TableCell sx={{ width: '25%' }} align="center">
                                                        Task
                                                    </TableCell>
                                                    <TableCell sx={{ width: '50%' }} align="center">
                                                        Description
                                                    </TableCell>
                                                    {/* <TableCell sx={{ width: '60px' }} align="center">
                                                        Due Date
                                                    </TableCell>
                                                    <TableCell sx={{ width: '60px' }} align="center">
                                                        Source
                                                    </TableCell> */}
                                                    <TableCell sx={{ width: '25%' }} align="center">
                                                        Status
                                                    </TableCell>
                                                </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                {tasks.map((item) => {
                                                    return (
                                                        <TableRow key={item._id} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                                            <TableCell align="center" scope="row">
                                                                <Typography sx={{ pt: 1 }} variant="h6">
                                                                    <Link
                                                                        style={{ textDecoration: 'none' }}
                                                                        to={`/dashboard/projectgoal?goalId=${item.goalId}&projectId=${item.projectId}`}
                                                                    >
                                                                        {item.name}
                                                                    </Link>
                                                                </Typography>
                                                            </TableCell>
                                                            <TableCell align="center" variant="h6">
                                                                {item.desc}
                                                            </TableCell>
                                                            {/* <TableCell align="center">
                                                                <Typography sx={{ pt: 1 }} variant="h6">
                                                                    {item.due}
                                                                </Typography>
                                                            </TableCell> */}
                                                            {/* <TableCell align="center">
                                                                <Typography sx={{ pt: 1 }} variant="h6">
                                                                    <a href={`${item.url}`} target="blank">
                                                                        {item.source}
                                                                    </a>
                                                                </Typography>
                                                            </TableCell> */}
                                                            <TableCell align="center">
                                                                <Tooltip title="Mark Completed">
                                                                    <Checkbox
                                                                        checked={item.status}
                                                                        onChange={handleTaskStatus(item.goalId, item._id)}
                                                                        inputProps={{ 'aria-label': 'controlled' }}
                                                                        color="success"
                                                                    />
                                                                </Tooltip>
                                                            </TableCell>
                                                        </TableRow>
                                                    );
                                                })}
                                            </TableBody>
                                        </Table>
                                    </TableContainer>
                                </Grid>
                            </Grid>
                        </TabPanel>
                        <TabPanel value={tabValue} index={2}>
                            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                                <Grid item xs={12}>
                                    <Typography sx={{ float: 'left' }} variant="h3">
                                        Team
                                    </Typography>
                                    <Button
                                        variant="contained"
                                        startIcon={<AddIcon />}
                                        sx={{ float: 'right' }}
                                        onClick={() => {
                                            getAllEmployees()
                                            setValue(2)
                                            setOpen((open) => !open)
                                        }}
                                    >
                                        Add a new user
                                    </Button>
                                </Grid>
                                <Grid item xs={12} >
                                    <List sx={{ p: 0 }}>
                                        {projectTeam.map((item, index) => {
                                            return (
                                                <>
                                                    <ListItem
                                                        id={item.uid}
                                                        key={item.uid}
                                                        sx={{ p: 0 }}
                                                        secondaryAction={
                                                            <IconButton
                                                                edge="end"
                                                                aria-label="delete"
                                                                sx={{ borderRadius: '50%' }}
                                                                onClick={() => {
                                                                    firestore
                                                                        .collection(collection)
                                                                        .doc(item.uid)
                                                                        .update({
                                                                            projects: firebase.firestore.FieldValue.arrayRemove(
                                                                                user.email
                                                                            )
                                                                        })
                                                                        .then((res) => {
                                                                            const teamMem = document.getElementById(
                                                                                item.uid
                                                                            );
                                                                            teamMem.style.display = 'none';
                                                                        });
                                                                }}
                                                            >
                                                                <DeleteIcon />
                                                            </IconButton>
                                                        }
                                                    >
                                                        <ListItemAvatar>
                                                            <Avatar alt="DP" src={item.photoURL} />
                                                        </ListItemAvatar>
                                                        <ListItemText primary={item.displayName} secondary={item.email} />
                                                    </ListItem>
                                                    <Divider />
                                                </>
                                            );
                                            }
                                        )}
                                    </List>
                                </Grid>
                            </Grid>
                        </TabPanel>
                        <TabPanel value={tabValue} index={3}>
                            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                                <Grid item xs={12}>
                                    <Typography sx={{ float: 'left' }} variant="h3">
                                        Feed
                                    </Typography>
                                </Grid>
                            </Grid>
                        </TabPanel>
                    </Box>
                </Grid>
            </Grid>
        </>
    );
};

export default SamplePage;
